*{
  margin: 0%;
  padding: 0%;
}

.App{
  background-color: #000000;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
}

.left{
  width: 30%;
  margin: 50px;
  display: flex;
  flex-direction: column;
}

.left-comp{
  background: #494949;
  margin: 10px;
  padding: 25px;
  border-radius: 20px;
}

.left-comp:hover {
  box-shadow: 8px 7px 16px 9px hsla(245, 68%, 28%, 0.43);
}

input[type="file"] {
  display: none;
}

.input-selected{
  background-color: #77FF95;
  color: white;
}

.input-not-selected{
  background-color: #FF8A00;
  color: white;
}

.custom-file-upload {
  display: inline-block;
  padding: 6px 12px;
  cursor: pointer;
  margin: auto;
  display: block;
  width: 120px;
  text-align: center;
}

/* collage button */

.collage-btn{
  position: relative;
  width: 180px;
  height: 43px;
  margin-left: auto;
  /* margin-right: auto; */
  margin-bottom: 10px;
  background: linear-gradient(106.26deg, rgba(255, 4, 4, 0.5) 15.67%, rgba(183, 107, 107, 0) 76.65%), #6821FF;
  border-radius: 66px;
  border: 0px;
  font-size: medium;
  font-weight: 600;
}

.collage-btn:hover{
  background: #6821FF;
  cursor: pointer;
}

/* Background Selection */

.collage-back{
  color: #ffffff;
  margin: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.back-col{
  height: 12px;
  width: 12px;
  border: 1px solid;
  padding: 0px;
}

/* <------------------Right---------------> */
.right{
  width: 100%;
}

.empty-collage{
  margin: 50px;
  width: 90%;
  background-color: #ffffff;
  height: 500px;
}

.collage{
  margin: 50px;
  width: 90%;
  height: 500px;
}

.column1, .column2{
  display: flex;
  flex-direction: row;
}

.column1{
  padding: 40px 40px 0px 40px;
}

.column2{
  margin: 0px 40px 40px 40px;
}

.image1, .image2{
  margin: 15px;
}

.image1{
  width: 50%;
}

.image2{
  width: 30%;
}

#imageDiv{
  width: 50%;
  height: 50%;
  overflow: hidden; /* essential, hides the overflow from the image */
}

#imageDiv img{
  width: 100%;
  height: 180px;/* makes image 100% width of the container */
  position: relative; /* allows movement inside the container */
      /* OPTIONAL: use this to adjust where the 'center' starts */
      /* only useful on images that will be clipped */
}

.download-btn{
  margin-top: 40px;
  margin-left: 200px;
}